.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.itemContainer {
  display: flex;
  align-items: center;
  justify-content: center;
}
.brand {
  font-size: 3rem;
  font-weight: 600;
  margin-right: 5rem;
}

.brand > h4 {
